import React from 'react';
import styled from "styled-components";
import '../../build-css/common.css';
import '../../build-css/contents.css';
import Header from '../../js/header';
import { Products } from '../../constants/Products'
import { useParams } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';
import Comment from '../Comment'

// [S] 
export const Mobile = ({children}) => {
  const isMobile = useMediaQuery({
    query : "(max-width:768px)"
  });  
  return <>{isMobile && children}</>
}
export const PC = ({children}) => {
  const isPc = useMediaQuery({
    query : "(min-width:769px)"
  });  
  return <>{isPc && children}</>
}
// [E] 

const Wrap = styled.div``;
const Div = styled.div``;
const Images = styled.img``;
const Bar = styled.div`
  display: flex;
  align-items: center;
`;
const Btn = styled.button`
  flex: 0 0 60px;
  height: 50px;
  background-image: url(data:image/svg+xml;base64,);
  background-size: cover;
  background-position: center center;
  background-color: transparent;
  border: none;
`
const Tit = styled.div`
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-break: break-all;
  text-align:center;
  flex: 0 0 60%;
  margin: 0 auto;
  padding:5px 60px 5px 0;
  font-weight:bold;
  font-size:16px;
`;

function ProductDetail(){
  const { num } = useParams();
  const result = Products.filter(list => list.id === Number(num));
  const navigate = useNavigate();

  return (
    <>
      <PC><Header/></PC>
      <div className='inner'>
        <Wrap className='product_top'>
          <Mobile>
            <Bar>
              <Btn onClick={() => navigate(-1)}></Btn>
              <Tit>{result[0].title}</Tit>
            </Bar>
          </Mobile>
          <Div className='thumb'>
            <Images src={result[0].img}/>
          </Div>
          <Div>
            <ul>
              <li className='type'>
                <em>{result[0].cate}</em>
                <em> : {result[0].id}</em>
              </li>
              <li className='title'>{result[0].title}</li>
              <li className='dimmedPrice'>
                <em>{result[0].discount}</em>
                <i>{result[0].dimmedPrice}</i>
              </li>
              <li className='price'>{result[0].price}</li>
            </ul>
            <table>
              <thead>
                <tr><td> </td></tr>
              </thead>
              <tbody>
                <tr>
                  <th></th>
                  <td>10%  </td>
                </tr>
                <tr>
                  <th></th>
                  <td>
                    M 5%  <br/>
                     6 
                  </td>
                </tr>
                <tr>
                  <th></th>
                  <td>
                     <br/>
                    <br/>
                    10/18()    
                  </td>
                </tr>
              </tbody>
            </table>
          </Div>
        </Wrap>
        <Comment/>
      </div>
    </>
  )
}
export default ProductDetail;